* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  padding-left: 0;
}

p {
  margin: 0;
}

.list {
  display: flex;
  list-style: none;
  align-items: center;
}

.list img {
  margin-right: 10px;
}

.list:not(:last-child) {
  margin-bottom: 10px;
}

.country-container {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.country-container img {
  margin-right: 10px;
}

.country-container p {
  font-weight: bold;
  font-size: 32px;
}

span {
  font-weight: bold;
  margin-right: 5px;
}